.content-contack {
    max-width: 100%;
    height: 100%;
    margin: 0 auto;
    background: #1F1C1C;
    padding: 0 10rem;
}

.content-con {
    display: flex;
    justify-content: center;
    width: auto;
    flex-wrap: wrap;
    padding: 1.5rem 1.5rem;
    gap: 5rem;
}

.con-Map iframe {
    width: 600px;
    height: 450px;
    border-radius: 10px;
}

.con-contact a {
    text-decoration: none !important;
}

.con-contact label {
    cursor: pointer;
}

.con-contact p {
    color: #FFCF01;
}

.con-contact label {
    color: #fff;
}

.telCall {
    text-decoration: none;
    color: #fff;
}

@media screen and (max-width: 1024px) {
    .content-contack {
        padding: 1.5rem 1.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .content-con {
        flex-wrap: wrap;

        .con-Map {
            padding-right: 10px;
        }
    }

    .con-Map iframe {
        width: 380px;
        height: 280px;
    }
}