.container {
  max-width: 100%;
  margin: 0 auto;
}

.cta-bg {
  background: url("https://images.unsplash.com/photo-1544161515-4ab6ce6db874?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1740&q=80") no-repeat;
  height: 450px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.liner {
  max-width: 100%;
  height: 0px;
  border: 3px solid #D4BC8C;
}

.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5)
}

.cta-text {
  position: relative;
  font-weight: bold;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  // height: 450px;
  text-align: center;
}

.cta-text a {
  text-decoration: none !important;
}

.cta-text label {
  cursor: pointer;
}

.cta-text p {
  color: #FFCF01;
}

.cta-text label {
  color: #fff;
}

.cta-btn {
  background: #fff;
  display: inline-block;
  padding: .5rem 1rem;
  text-decoration: none;
  color: #333;
  border: 2px solid transparent;
  transition: 0.3s;
  margin-top: 1rem;
  border-radius: 5px;
  text-transform: uppercase;
}

.cta-btn:hover {
  border: 2px solid #fff;
  background: transparent;
  color: #fff;
}

.cta-text img {
  max-width: 200px;
  max-height: 300px;
}

@media screen and (max-width: 480px) {
  .cta-text {
    // padding: 0 2rem;
  }
}